"use client";

import { motion } from "framer-motion";
import { SectionTitle, Card, Button } from "@/components/ui";
import { 
  Target, 
  Eye, 
  Heart, 
  Lightbulb, 
  Shield, 
  Users,
  Linkedin,
  Twitter,
  Github
} from "lucide-react";
import Link from "next/link";

const values = [
  {
    icon: Lightbulb,
    title: "Innovation",
    description: "Selalu mengadopsi teknologi dan pendekatan terbaru untuk solusi terbaik.",
  },
  {
    icon: Shield,
    title: "Quality",
    description: "Komitmen pada kualitas tinggi di setiap baris kode dan desain.",
  },
  {
    icon: Heart,
    title: "Integrity",
    description: "Transparansi dan kejujuran dalam setiap aspek kerja sama.",
  },
  {
    icon: Users,
    title: "Customer-First",
    description: "Kepuasan dan kesuksesan klien adalah prioritas utama kami.",
  },
];

const team = [
  {
    name: "Ahmad Sakama",
    role: "Founder & CEO",
    image: null,
    social: { linkedin: "#", twitter: "#", github: "#" },
  },
  {
    name: "Budi Santoso",
    role: "Lead Developer",
    image: null,
    social: { linkedin: "#", github: "#" },
  },
  {
    name: "Citra Dewi",
    role: "UI/UX Designer",
    image: null,
    social: { linkedin: "#", twitter: "#" },
  },
  {
    name: "Dian Pratama",
    role: "Full Stack Developer",
    image: null,
    social: { linkedin: "#", github: "#" },
  },
];

const techStack = [
  "React", "Next.js", "TypeScript", "Node.js", 
  "Flutter", "React Native", "PostgreSQL", "MongoDB",
  "Tailwind CSS", "Figma", "AWS", "Vercel"
];

export default function AboutPage() {
  return (
    <>
      {/* Hero Section */}
      <section className="py-24 gradient-hero">
        <div className="container mx-auto px-4 lg:px-8">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
            className="max-w-3xl mx-auto text-center"
          >
            <span className="inline-block px-4 py-1.5 text-xs font-semibold text-primary bg-primary-50 rounded-full mb-4">
              Tentang Kami
            </span>
            <h1 className="text-4xl md:text-5xl font-bold text-foreground mb-6">
              Building Digital Excellence Since 2019
            </h1>
            <p className="text-lg text-muted-foreground leading-relaxed">
              Sakama System adalah software house yang berdedikasi untuk membangun 
              solusi digital berkualitas tinggi bagi bisnis di seluruh Indonesia.
            </p>
          </motion.div>
        </div>
      </section>

      {/* Vision & Mission */}
      <section className="py-24">
        <div className="container mx-auto px-4 lg:px-8">
          <div className="grid md:grid-cols-2 gap-12">
            <motion.div
              initial={{ opacity: 0, x: -20 }}
              whileInView={{ opacity: 1, x: 0 }}
              viewport={{ once: true }}
              transition={{ duration: 0.5 }}
            >
              <Card hover={false} className="h-full">
                <div className="w-14 h-14 rounded-2xl bg-primary-50 flex items-center justify-center mb-6">
                  <Eye className="w-7 h-7 text-primary" />
                </div>
                <h2 className="text-2xl font-bold text-foreground mb-4">Visi</h2>
                <p className="text-muted-foreground leading-relaxed">
                  Menjadi mitra teknologi terdepan yang memberdayakan bisnis Indonesia 
                  untuk berkembang di era digital melalui solusi inovatif dan berkualitas.
                </p>
              </Card>
            </motion.div>

            <motion.div
              initial={{ opacity: 0, x: 20 }}
              whileInView={{ opacity: 1, x: 0 }}
              viewport={{ once: true }}
              transition={{ duration: 0.5, delay: 0.1 }}
            >
              <Card hover={false} className="h-full">
                <div className="w-14 h-14 rounded-2xl bg-primary-50 flex items-center justify-center mb-6">
                  <Target className="w-7 h-7 text-primary" />
                </div>
                <h2 className="text-2xl font-bold text-foreground mb-4">Misi</h2>
                <ul className="text-muted-foreground leading-relaxed space-y-2">
                  <li>• Menyediakan solusi digital yang tepat guna dan berkualitas</li>
                  <li>• Membangun hubungan jangka panjang dengan klien</li>
                  <li>• Terus berinovasi mengikuti perkembangan teknologi</li>
                  <li>• Mengembangkan talenta digital Indonesia</li>
                </ul>
              </Card>
            </motion.div>
          </div>
        </div>
      </section>

      {/* Values */}
      <section className="py-24 bg-muted">
        <div className="container mx-auto px-4 lg:px-8">
          <SectionTitle
            badge="Core Values"
            title="Nilai-Nilai Kami"
            description="Prinsip yang menjadi fondasi dalam setiap pekerjaan kami."
          />

          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
            {values.map((value, index) => (
              <motion.div
                key={value.title}
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.5, delay: index * 0.1 }}
              >
                <Card className="h-full text-center">
                  <div className="w-14 h-14 rounded-2xl bg-primary-50 flex items-center justify-center mb-4 mx-auto">
                    <value.icon className="w-7 h-7 text-primary" />
                  </div>
                  <h3 className="text-lg font-semibold text-foreground mb-2">
                    {value.title}
                  </h3>
                  <p className="text-sm text-muted-foreground">
                    {value.description}
                  </p>
                </Card>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* Team */}
      <section className="py-24">
        <div className="container mx-auto px-4 lg:px-8">
          <SectionTitle
            badge="Our Team"
            title="Tim Profesional Kami"
            description="Developer dan desainer berpengalaman yang siap mewujudkan visi digital Anda."
          />

          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-8">
            {team.map((member, index) => (
              <motion.div
                key={member.name}
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.5, delay: index * 0.1 }}
                className="text-center"
              >
                <div className="w-32 h-32 rounded-full bg-gradient-to-br from-primary to-primary-dark mx-auto mb-4 flex items-center justify-center">
                  <span className="text-4xl font-bold text-white">
                    {member.name.charAt(0)}
                  </span>
                </div>
                <h3 className="text-lg font-semibold text-foreground mb-1">
                  {member.name}
                </h3>
                <p className="text-sm text-muted-foreground mb-4">
                  {member.role}
                </p>
                <div className="flex justify-center gap-3">
                  {member.social.linkedin && (
                    <a href={member.social.linkedin} className="text-muted-foreground hover:text-primary transition-colors">
                      <Linkedin className="w-5 h-5" />
                    </a>
                  )}
                  {member.social.twitter && (
                    <a href={member.social.twitter} className="text-muted-foreground hover:text-primary transition-colors">
                      <Twitter className="w-5 h-5" />
                    </a>
                  )}
                  {member.social.github && (
                    <a href={member.social.github} className="text-muted-foreground hover:text-primary transition-colors">
                      <Github className="w-5 h-5" />
                    </a>
                  )}
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* Tech Stack */}
      <section className="py-24 bg-muted">
        <div className="container mx-auto px-4 lg:px-8">
          <SectionTitle
            badge="Technologies"
            title="Tech Stack Kami"
            description="Teknologi modern yang kami gunakan untuk membangun solusi digital."
          />

          <motion.div
            initial={{ opacity: 0 }}
            whileInView={{ opacity: 1 }}
            viewport={{ once: true }}
            transition={{ duration: 0.5 }}
            className="flex flex-wrap justify-center gap-4"
          >
            {techStack.map((tech, index) => (
              <motion.span
                key={tech}
                initial={{ opacity: 0, scale: 0.9 }}
                whileInView={{ opacity: 1, scale: 1 }}
                viewport={{ once: true }}
                transition={{ duration: 0.3, delay: index * 0.05 }}
                className="px-6 py-3 bg-card border border-border rounded-full text-sm font-medium text-foreground hover:border-primary hover:text-primary transition-colors cursor-default"
              >
                {tech}
              </motion.span>
            ))}
          </motion.div>
        </div>
      </section>

      {/* CTA */}
      <section className="py-24">
        <div className="container mx-auto px-4 lg:px-8 text-center">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.5 }}
          >
            <h2 className="text-3xl md:text-4xl font-bold text-foreground mb-4">
              Tertarik Bekerja Sama?
            </h2>
            <p className="text-muted-foreground mb-8 max-w-xl mx-auto">
              Mari diskusikan bagaimana kami dapat membantu transformasi digital bisnis Anda.
            </p>
            <Link href="/contact">
              <Button size="lg">Hubungi Kami</Button>
            </Link>
          </motion.div>
        </div>
      </section>
    </>
  );
}
